import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

import Loader from '../components/Loader';

import { LocationCreate } from '../api';
import { useSnackbar } from '../hooks';
import { CreateLocation } from '../utils/dataTypes';

const validationSchema = yup.object({
  zipCode: yup.string().required('Zip Code is required'),
  name: yup.string().required('Name is required'),
  stateCode: yup.string().required('State Code is required'),
  stateName: yup.string().required('State Name is required'),
  location: yup
    .tuple([
      yup
        .number()
        .typeError('Latitude is required')
        .min(-90, 'Latitude must be grater than or equal: -90')
        .max(90, 'Latitude must be less than or equal: 90')
        .required('Location should be in format "latitude, longitude"'),
      yup
        .number()
        .typeError('Longitude is required')
        .min(-180, 'Longitude must be grater than or equal: -180')
        .max(180, 'Longitude must be less than or equal: 180')
        .required('Location should be in format "latitude, longitude"'),
    ])
    .required('Location is required'),
});

export default function CreateLocationForm() {
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const { mutate, isLoading } = useMutation(LocationCreate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Created',
      });
      queryClient.invalidateQueries(['locations']);
      navigate(-1);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateLocation>({
    initialValues: {
      zipCode: '',
      name: '',
      stateCode: '',
      stateName: '',
      location: [NaN, NaN],
    },
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values as unknown as CreateLocation);
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  console.log(formik.errors.location);

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box sx={{ p: 3, display: 'grid' }}>
          <Box key="controls" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <Box key="name" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="name"
                  name="name"
                  label="Name"
                  value={formik.values.name}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={formik.touched.name && Boolean(formik.errors.name)}
                  helperText={formik.touched.name && formik.errors.name}
                />
              </Box>
              <Box key="stateName" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="stateName"
                  name="stateName"
                  label="State Name"
                  value={formik.values.stateName}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={
                    formik.touched.stateName && Boolean(formik.errors.stateName)
                  }
                  helperText={
                    formik.touched.stateName && formik.errors.stateName
                  }
                />
              </Box>
              <Box key="latitude" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="latitude"
                  name="location[0]"
                  label="Latitude"
                  value={
                    isNaN(formik.values.location[0])
                      ? ''
                      : formik.values.location[0]
                  }
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={
                    formik.touched.location &&
                    Boolean(formik.errors.location && formik.errors.location[0])
                  }
                  helperText={
                    formik.touched.location &&
                    formik.errors.location &&
                    formik.errors.location[0]
                  }
                />
              </Box>
            </Box>
            <Box key="right" sx={{ p: 1, width: '50%', height: '75px' }}>
              <Box key="zipCode" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="zipCode"
                  name="zipCode"
                  label="Zip Code"
                  value={formik.values.zipCode}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={
                    formik.touched.zipCode && Boolean(formik.errors.zipCode)
                  }
                  helperText={formik.touched.zipCode && formik.errors.zipCode}
                />
              </Box>
              <Box key="stateCode" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="stateCode"
                  name="stateCode"
                  label="State Code"
                  value={formik.values.stateCode}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={
                    formik.touched.stateCode && Boolean(formik.errors.stateCode)
                  }
                  helperText={
                    formik.touched.stateCode && formik.errors.stateCode
                  }
                />
              </Box>
              <Box key="longitude" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="longitude"
                  name="location[1]"
                  label="Longitude"
                  value={
                    isNaN(formik.values.location[1])
                      ? ''
                      : formik.values.location[1]
                  }
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={
                    formik.touched.location &&
                    Boolean(formik.errors.location && formik.errors.location[1])
                  }
                  helperText={
                    formik.touched.location &&
                    formik.errors.location &&
                    formik.errors.location[1]
                  }
                />
              </Box>
            </Box>
          </Box>
          <Box key="buttons" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
              >
                Create
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading} />
    </Box>
  );
}
